@font-face {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    src: url(/static/media/OpenSans-Regular.3ed9575d.ttf); /* IE9 Compat Modes */
    src: local('Open Sans'), local('OpenSans'),
         url(/static/media/OpenSans-Regular.3ed9575d.ttf) format('embedded-opentype'), 
         
         
         url(/static/media/OpenSans-Regular.3ed9575d.ttf) format('truetype'), /* Safari, Android, iOS */
         /* url('../fonts/open-sans-v13-latin-regular.svg#OpenSans') format('svg'); Legacy iOS */
  }

:root {
    --red: #f00;
    --green: #0f0;
    --blue: #00f;
}  

html,body{
    overflow: hidden;
}
body {
    margin: 0;
    font-family: 'Open Sans' !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.form-button{
    display: block;
width: 100%;
border-radius: 4px !important;
background-image: linear-gradient(#009FDB, #004759) !important;
height: 29px;
min-height: 25px;
}
.routes {
    background: #fff;
}

.components-main {
    webkit-box-align: center;
    align-items: center;
    display: flex !important;
    flex-direction: column;
    height: 100%;
    min-height: 100vh;
    padding: 40px 40px 20px;
    width: 100%;
}

.login-form,.sign-up-form{
    background: #fff;
    color: #3a3b3c;
    border-radius: 2px;
    box-shadow: 0 5px 30px 0 rgba(0, 0, 0, .15);
    padding: 4% 8% 8% 8%;
    position: relative;
    width: 450px;
}

.makeStyles-appBar-4 {
    background: #262728 ;
    height: 60px;
    overflow: hidden;
}

.MuiDrawer-root .MuiDrawer-paper,
.MuiAppBar-colorPrimary {
    background: #121314 ;
    color: #fff;
}

.MuiButton-containedPrimary {
    color: #fff;
    background-color: #044768 !important;
}

.MuiIconButton-label {
    /* color: #fff !important; */
}

